* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    line-height: 1.6;
  }

  a {
    font-weight: 900;
    text-decoration: none;
    color: black;
  }
  
  a:visited {
    text-decoration: none;
  }
  
  a:hover {
    text-shadow: 0 3px 10px rgb(12 55 69 / 0.8);
  }
  
  a:active {
    text-decoration: underline;
  }
  
  .y-sticky {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100vh;
  }